// 全局注册
import Vue from 'vue'

import App from './App.vue'
import router from './router'
import store from './store'

import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

// mavon-editor是一款基于vue的markdown编辑器，比较适合博客系统
import mavonEditor from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'

// 字体图标
import './assets/font/iconfont.js'

// animate.css是一个使用CSS3的animation制作的动画效果的CSS集合，里面预设了很多种常用的动画，且使用非常简单
import animated from 'animate.css' // npm install animate.css --save安装，在引入
// github-markdown-css 一般用做文章正文的样式美化
import "github-markdown-css";
// highlight.js是一个用于在任何web页面上 着色显示各种示例源代码语法 的JS项目
import hljs from 'highlight.js'
import 'highlight.js/styles/atom-one-dark.css'


export const highlightCode = () => {
  // pre标签   识别json对象中的\n\t等转义字符，展示原始的JSON对象结构
  const preEl = document.querySelectorAll('pre')

  preEl.forEach((el) => {
    // 代码高亮
    hljs.highlightBlock(el)
  })
}


// 挂载为Vue的插件，全局可用
Vue.use(animated)

Vue.use(mavonEditor)
Vue.use(ElementUI);

// 阻止启动生产消息
Vue.config.productionTip = false


new Vue({
  router,
  store,
  render: h => h(App)
  
  //  render: h => h(App)是下面内容的缩写
// render: function (createElement) {
//   return createElement(App);
// }
// h是 Vue.js 里面的 createElement 函数，这个函数的作用就是生成一个 VNode节点，render 函数得到这个 VNode 节点之后，返回给 Vue.js 的 mount 函数，渲染成真实 DOM 节点，并挂载到根节点上。

}).$mount('#app')





