<!--  一个标准的vue组件，包含三个部分，一个是模板，一个是script，一个是样式 -->
<template>
  <div id="app">
    <!-- 路由占位符 -->
    <router-view />
  </div>
</template>

<script>
import Blog from "@/views/Blog.vue";
// import {
//   mapGetters
// } from "vuex";

export default {
  name: "App",
  components: {
    Blog,
  },
  // 作为父组件，提供给子组件数据，在子组件中用inject接收
  provide() {
    // 通过provide为子组件提供页面刷新函数，
    // 在需要进行刷新页面的子组件中用inject获得该函数
    return {
      reload: this.reload,
    };
  },

  data() {
    return {
      isReload: true,
    };
  },
  // 生命周期函数  最早在mounted里面可以操作DOM元素！！！
  mounted() {
    document.getElementById("app").style.display = "block";
    // appLoading  index.html中   ？？？？？？？？？
    document.getElementById("appLoading").style.display = "none";
  },
  methods: {
    // 用户执行完某个动作，改变了某些状态，需要重新刷新页面，以此来重新渲染页面
    //  reload()
    reload() {
      this.isReload = false;

      // 等组件的DOM 更新完成之后，再执行 cb 回调函数，
      // 从而能保证 cb 回调函数里面可以操作到最新的 DOM 元素。
      this.$nextTick(() => {
        this.isReload = true;
      });
    },
  },
};
</script>


<style lang="scss">
@import './assets/css/base.scss';
@import './assets/css/mixin.scss';
@import './assets/font/iconfont.css';

#app {
  width: 100%;
  @include bg_app_color();
