const routes = [
  { path: '/', component:()=> import('../views/Blog.vue'), 
    children: [
      // 子路由不需要加/
      // 默认子路由
      { path: '', component:()=> import('../views/Home.vue') },
      { path: 'home', component:()=> import('../views/Home.vue')  },
      { path: 'Life', component: ()=> import('../views/Life.vue') },
      { path: 'Articles', component:()=> import('../views/Articles.vue')},
      { path: 'mesg', component:()=> import('../views/Message.vue') },
      { path: 'Resources', component: ()=> import('../views/Resources.vue')  },
      { path: 'about', component: () => import('../views/About.vue')  },
      { path: 'detail', component: () => import('../views/ShowArt.vue') },
    ]
  },
  { path: '/login', component:() => import('../views/Manage/Login.vue') },
  { path: '/edit', component: () => import('../views/Manage/Edit.vue'),
    children: [
      // 这里有点问题的感觉  这里就一定要加/
      { path: '', component: () => import('../components/Manage/CreateArt.vue')},
      { path: '/editart', component: ()=>import('../components/Manage/EditArticle.vue') },
      { path: '/editlife', component: () => import('../components/Manage/EditLife.vue') },
      { path: '/addart', component: () => import('../components/Manage/CreateArt.vue') },
      { path: '/editmeg', component: () => import('../components/Manage/EditMeg.vue') },
    ]
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
